* {
    padding: 0;
    margin: 0;
}

.flex {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  -webkit-flex-flow: row wrap;
  flex-flow: row wrap;

  font-weight: bold;
  text-align: center;
}

.flex > * {
  padding: 10px;
  flex: 1 100%;
}

.header {
  background: red;
}

.footer {
  background: red;
}

.main {
  text-align: left;
  background: white;
}

.aside-1 {
  background: white;
}

.aside-2 {
  background: white;
}

@media all and (min-width: 600px) {
  .aside { flex: 1 auto; }
}

@media all and (min-width: 800px) {
  .main    { flex: 3 0px; }
  .aside-1 { order: 1; }
  .main    { order: 2; }
  .aside-2 { order: 3; }
  .footer  { order: 4; }
}

h1 {
  float: left;
  color: white;
}

ul {
  float: right;
  list-style: none;

li {
  display: inline-block;
  padding: 10px;
} a {
  text-decoration: none;
  color: white;
  }
}

footer a {
  text-decoration: none;
  color: white;
}

a:hover {
  color: black;
}
